import React, { Component } from 'react';

import axios from 'axios';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import Typography from '@material-ui/core/Typography';

import queryString from 'query-string';

class TideTimes extends Component {

    state = {
        loading: false,
        siteId: 'salcombe',
    }

    constructor(props) {
        super(props);
        const values = queryString.parse(this.props.location.search);
        if (values.siteId) {
            this.state.siteId = values.siteId;
        }
        console.log("TideTimes.constructor() Site = " + this.state.siteId);
    }

    componentDidMount() {
        const values = queryString.parse(this.props.location.search);
        console.log("TideTimes.componentDidMount() Site = " + values.siteId);
        this.loadData(values.siteId);
    }

    componentWillReceiveProps(nextProps) {
        console.log("TideTimes.componentWillReceiveProps() props = ");
        console.log(nextProps);
        const values = queryString.parse(nextProps.location.search);
        this.loadData(values.siteId ? values.siteId : this.state.siteId);
    }

    loadData = (siteId) => {
        siteId = siteId ? siteId : 'salcombe';
        console.log("TideTimes.loadData() siteId = " + siteId);
        this.setState({ loading: true, siteId: siteId });
        axios.get("https://opalescent-radiator.glitch.me?locationId=" + siteId, { crossdomain: true })
            .then(response => {
                console.log(response.data); //#body_id
                const json = response.data;
                this.setState({
                    siteId: siteId,
                    loading: false,
                    json
                });
            })
            .catch(error => {
                console.error("error: ", error);
                this.setState({
                    siteId: siteId,
                    error: `${error}`,
                    loading: false
                });
            });
    }

    ucfirst(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    render() {

        console.log("TideTimes.render() siteId = " + this.state.siteId);

        const { loading, error, siteId, json } = this.state;

        if (!siteId) {
            return <Typography variant="h3">No Site ID</Typography>
        }

        if (loading) {
            return <Typography variant="h3">Loading...</Typography>
        }

        if (!json) {
            return <Typography variant="h3"></Typography>
        }

        if (error) {
            return (
                <Typography variant="h4">
                    There was an error loading the repos.{" "}
                    <button onClick={this.loadData}>Try again</button>
                </Typography>
            );
        }

        var isHeightLow = [];
        if (json[siteId]) {
            console.log(json)
            var startLow = json[siteId][0]['height'] < json[siteId][1]['height'];
            var currentRelHeight = startLow;
            json[siteId].forEach((element, i) => {
                isHeightLow.push(currentRelHeight);
                currentRelHeight = !currentRelHeight;
            });
        }
        return (
            <div>
                <Typography variant="h4">{this.ucfirst(siteId)}</Typography>
                <Typography variant="caption">{new Date().toDateString()}</Typography>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell><Typography variant="h5">Time</Typography></TableCell>
                            <TableCell><Typography variant="h5">Height (meters)</Typography></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            (json[siteId]) ?
                                json[siteId].map((obj, i) => {
                                    return (
                                        obj ?
                                            <TableRow key={i}>
                                                <TableCell><Typography variant="h6">{obj.time}</Typography></TableCell>
                                                <TableCell>
                                                    <Typography variant="h6">
                                                        {obj.height} ({isHeightLow[i] ? "L" : "H"})
                                                    </Typography>
                                                </TableCell>
                                            </TableRow>
                                            : <TableRow key={i}></TableRow>
                                    );
                                })
                                : null
                        }
                    </TableBody>
                </Table>
            </div>
        );
    }
}

export default TideTimes;